.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: #fff;
    border-radius: 8px;
    padding: 20px;
    text-align: center;
    position: relative;
    max-width: 90%;
    max-height: 90%;
  }
  
  .modal-image {
    max-width: 100%;
    max-height: 80vh;
    border-radius: 8px;
  }
  
  .photo-name {
    margin-top: 10px;
    font-size: 16px;
    font-weight: bold;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #333;
  }
  
  .close-button:hover {
    color: #f00;
  }
  